import { lazy, Suspense, useContext } from "react";
import { Switch } from "react-router-dom";

import { SharedDataCtx } from "@miq/contexts";
import { Loading } from "@miq/components";

import { AdminRoute, AdminView, AdminNavLink } from "@miq/adminjs";

import "./index.scss";

const EmployeeRoutes = lazy(() => import("./employee"));
const DepartmentRoutes = lazy(() => import("./department"));

export default function HrmRoutes(props) {
    const { path } = props.match;
    const { company } = useContext(SharedDataCtx);

    if (!company) {
        return <CompanyCreateView {...props} />;
    }

    return (
        <AdminView>
            <div className="d-flex">
                <div className="flex-1">
                    <Suspense fallback={<Loading />}>
                        <Switch>
                            <AdminRoute
                                path={`${path}:compSlug/employees/`}
                                component={EmployeeRoutes}
                                requiredPerms={["miq_hrm.view_employee"]}
                            />
                            <AdminRoute
                                path={`${path}:compSlug/departments/`}
                                component={DepartmentRoutes}
                                requiredPerms={["miq_hrm.view_department"]}
                            />
                            <AdminRoute
                                path={path}
                                component={CompanyEditView}
                                requiredPerms={["miq_hrm.change_company"]}
                            />
                        </Switch>
                    </Suspense>
                </div>

                <div className="p-2">
                    <div className="">
                        <AdminNavLink label="Profile" to={`${path}`} />
                        <AdminNavLink label="Employees" to={`${path}${company.slug}/employees/`} />
                        <AdminNavLink label="Departments" to={`${path}${company.slug}/departments/`} />
                    </div>
                </div>
            </div>
        </AdminView>
    );
}

/**
 * COMPANY
 */

const CompanyCreateView = (props) => {
    return <AdminView title="Add company"></AdminView>;
};

const CompanyEditView = (props) => {
    const { company } = useContext(SharedDataCtx);

    return (
        <AdminView title="Company">
            <div className="">{company.name}</div>

            <div className="">
                <div className="">Segmentation</div>
                <div className="text-muted text-sm">All Users | % Male/Female</div>
            </div>
        </AdminView>
    );
};
